<template>
    <viewer class="bear-head" :style="{'background-image': 'url('+bgurl+')'}">
        <div class="bear-tips" v-if="(tipArray[Math.floor(Math.random()*9+1)])"></div>
    </viewer>
</template>

<script>
export default {
  name: 'bear-head',
  data() {
    return {
      msg: '',
      bgurl: '',
      tipArray: ['敌进我退，敌驻我扰，敌疲我打，敌退我追'],
    };
  },
  created(){
      // 随机 0-9 的图片作为 背景图 需要require才行
    let bg = Math.floor(Math.random()*9+1);
    this.bgurl = require('../../assets/image/bg'+bg+'.jpg')
  }
};
</script>

<style lang="scss" scoped>
.bear-head{
    position: relative;
    top:0;
    .bear-tips{
        font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;
        width: 50%;
        height: 60px;
        line-height: 60px;
        font-size: 45px;
        color: #fff;
        opacity: 0.9;
        position: absolute;
        left: 25%;
        top: calc( 280px - 30px);
        background: rgba(255,255,255,0.2);
        text-align: center;
        border:1px solid #fff;
        border-radius: 5px;
    }
}
</style>



